

#recommend_music_page{
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	left: 0px;
	top: 0px;
	background: #f8f8f8;
	overflow-y:scroll;
	overflow-x:hidden;
	opacity: 0;
}
.recommend_music_page_top{
	position: relative;
	width: 100%;
	height: 170px;
}
.recommend_music_page_top i{
	display: block;
	position: absolute;
	left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.25);
    background-position: 50%;
    -webkit-filter: blur(20px);
    filter: blur(20px);
    -webkit-transform: scale(1.5);
    -ms-transform: scale(1.5);
        transform: scale(1.5);
}
.recommend_music_page_top strong{
	position: absolute;
	left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.20);
}
.recommend_music_page_top .imgBox{
	width: 114px;
	height: 114px;
	position: absolute;
	z-index: 3;
	left: 5%;
	top: 16%;
}
.recommend_music_page_top .imgBox span{
	position: absolute;
    z-index: 3;
    top: 10px;
    left: 0;
    padding: 0 8px;
    height: 17px;
    color: #fff;
    font-size: 9px;
    text-align: center;
    line-height: 17px;
    background-color: rgba(217,48,48,.8);
    border-top-right-radius: 17px;
    border-bottom-right-radius: 17px;
}
.recommend_music_page_top h2{
	position: absolute;
	left: 46%;
	top: 20%;
	color: #fefefe;
	z-index: 3;
	font-weight: normal;
	padding-top: 1px;
    font-size: 17px;
    line-height: 1.3;
    display: block;
    width:50%;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.musicMenuAuthor{
	position: absolute;
	z-index: 3;
	left: 45%;
	top: 50%;
	width: 50%;
}
.musicMenuAuthor .musicMenuAuthor_img{
	display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 5px;
    overflow: hidden;
    float: left;
}
.musicMenuAuthor span{
	display: block;
	color: hsla(0,0%,100%,.7);
	font-size: 4vw;
	line-height: 250%;
	width:70%;
	float: left;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
   

.recommend_music_page_top h2:after{
	display: none;
}

.recommend_music_page_intro{
	position: relative;
	z-index: 2;
	width: 94%;
	height: 70px;
	padding: 3% 15px;
	background: #f8f8f8;
	color: #666;
}
.music_menu_tags span{
	float: left;
	font-size: 4vw;
	margin-right: 10px;
	margin-bottom: 10px;
}
.music_menu_tags ul li{
	float: left;
	font-size: 3.6vw;
	display: inline-block;
    margin-right: 10px;
    padding: 1px 8px;
    position: relative;
}
.music_menu_tags ul li:after{
	position: absolute;
    z-index: 2;
    content: "";
    top: 0;
    left: 0;
    pointer-events: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transform-origin: top left;
    -ms-transform-origin: top left;
        transform-origin: top left;
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 9999px;
	width: 200%;
    height: 200%;
    -webkit-transform: scale(.5);
    -ms-transform: scale(.5);
        transform: scale(.5);
}
	
.music_menu_description{
	clear: both;
	font-size: 4.5vw;
	width: 100%;
	display: -webkit-box; 
    -webkit-line-clamp: 2; 
    overflow: hidden; 
	/* autoprefixer: off */
	-webkit-box-orient: vertical; 
}


#recommend_music_page h3{
	height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
}
#recommend_music_page_songList{
	width: 100%;
}
#recommend_music_page_songList ul li{
	width: 100%;
	display: block;
	height: 50px;
	position: relative;
	padding: 2% 0px 1% 0px;
	border-bottom: #ddd 1px solid;
}
#recommend_music_page_songList ul li:before{
	content: '';
	display: inline-block;
	position: absolute;
	right: 3%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
    width: 22px;
    height: 22px;
    background: url('../image/index_icon_2x.png') no-repeat;
    background-position: -24px 0;
    background-size: 166px 97px;
}
#recommend_music_page_songList ul li b{
	display: block;
	width:12%;
	color: #999;
	height: 100%;
	line-height: 50px;
	font-weight: normal;
	font-size: 5vw;
	text-align: center;
	float: left;

}
#recommend_music_page_songList ul li p{
	display: block;
	width: 70%;
	height: 50%;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    font-size: 5.3vw;
    line-height: 25px;
}

#recommend_music_page_songList ul li strong{
	font-size: 12px;
    color: #888;
    font-weight: normal;
    display: block;
    height: 50%;
    width: 80%;
    line-height: 25px;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.ShowMove{
	opacity: 0;
	-webkit-animation: myfirst .5s forwards;
	        animation: myfirst .5s forwards;
}	
@-webkit-keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}


