*{
	margin: 0px;
	padding: 0px;
	-webkit-tap-highlight-color:rgba(0,0,0,0);

}
html,body{
	width: 100%;
	height: 100%;
}
body{
	overflow: hidden;
}
a{
	text-decoration: none;
}
ul li{
	list-style: none;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	font-size:4vw;
	color: #bfbfbf;
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	font-size:4vw;
	color: #bfbfbf;
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	font-size:4vw;
	color: #bfbfbf;
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	font-size:4vw;
	color: #bfbfbf;
}



#root{
	width: 100%;
	height: 100%;
	position: relative;
}
.selected{
	border-bottom: .3vh solid #d33a31;
	color: #d33a31 !important;
}
.nav{
	width: 100%;
	height:7vh;
	border-bottom: 1px solid #dedede;
}
.nav li{
	display: block;
	width: 33.33%;
	height: 100%;
	float: left;
}
.nav a{
	display: block;
	width:60%;
	line-height: 7vh;
	text-align: center;
	color: #333;
	height: 100%;
	margin: 0 auto;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
}


.nav a span{
	display: block;
	width: 100%;
	text-align: center;
	font-size: 4.8vw;
	height: 100%;
}





