#playsongPage{
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	left: 0px;
	top: 0px;
	background: #ccc;
	opacity: 0;
}
.ShowMove{
	opacity: 0;
	-webkit-animation: myfirst .5s forwards;
	        animation: myfirst .5s forwards;
}	
@-webkit-keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}
.playsongPage_bg{
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 1;
	left: 0px;
	top:0px;
    -webkit-transform: scale(1.5);
    -ms-transform: scale(1.5);
        transform: scale(1.5);
    -webkit-transform-origin: center;
    -ms-transform-origin: center;
        transform-origin: center;
    -webkit-filter: blur(20px);
   	filter: blur(20px); 
 

}
.playsongPage_bg:after{
	content: " ";
    bottom: 0;
    top: 0;
    background-color: rgba(0,0,0,.5);
	position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
}
.playsongPage_logo{
	content: '';
	position: absolute;
    top: 12px;
    left: 10px;
    display: block;
    width: 100px;
    height: 17px;
    background: url('../image/logo.png') no-repeat;
    background-size: 100px auto;
	z-index: 2;
}
.playsongPage_needle{
	content: " ";
    position: absolute;
    top:0px;
    left: 43%;
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transform-origin: left top;
    -ms-transform-origin: left top;
    transform-origin: left top;
    z-index: 5;
    width: 84px;
    height: 122px;
    background: url('../image/needle.png') no-repeat;
    background-size: contain;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.playsongPage_disc{
	position: relative;
    width: 248px;
    height: 248px;
    margin: 63px auto 0px;
    z-index: 4;
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
}
.playsongPage_disc .disc_border{
	position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background: url('../image/disc.png') no-repeat;
    background-size: contain;
}
.playsongPage_disc .disc_img{
	position: absolute;
    width: 150px;
    height: 150px;
    left: 50%;
    top: 50%;
    z-index: 1;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    overflow: hidden;
    background: url(//s3.music.126.net/m/s/img/disc_default.png?7c9b3ad…) no-repeat;
    background-size: contain;
}
.playsongPage_disc .disc_img img{
	width: 100%;
	vertical-align: middle;
}
.playsongBtn{
	position: absolute;
    width: 50px;
    height: 50px;
    left: 50%;
    top: 190px;
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
        transform: translate(-50%,-50%);
    z-index: 10;
    background: url('../image/play.png') 0 0 no-repeat;
    background-size: contain;
    display: none;
}
.artistsText{
	position: absolute;
	bottom: 25%;
	left: 50%;
	display: block;
	text-align: center;
	width: 80%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	color: #fefefe;
	font-size: 4.5vw;
	opacity: .6;
	z-index: 4;
	word-break: break-all; 
	word-wrap:break-word;
}
.lyricText{
	position: absolute;
	bottom: 15%;
	left: 50%;
	display: block;
	text-align: center;
	width: 80%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	color: #fefefe;
	font-size: 5.5vw;
	opacity: .7;
	z-index: 4;
	word-break: break-all; 
	word-wrap:break-word;
}
.lyricText b{
	font-weight: normal;
	opacity: 0;
}
.lyricShowMove{
	opacity: 0;
	-webkit-animation: myfirsts .3s forwards;
	        animation: myfirsts .3s forwards;
}	
@-webkit-keyframes myfirsts
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirsts
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}
.PlayRotateMove{
	-webkit-animation: rotateMove 15s linear infinite;
	        animation: rotateMove 15s linear infinite;
}
.pauseRotateMove{
	animation-play-state:paused !important;
    -webkit-animation-play-state:paused !important;
	-ms-animation-play-state:paused !important;
	-o-animation-play-state:paused !important;
}

@-webkit-keyframes rotateMove
{
    100%{
    	-webkit-transform: rotate(1turn);
		-ms-transform: rotate(1turn);
		transform: rotate(1turn);
    }
}

@keyframes rotateMove
{
    100%{
    	-webkit-transform: rotate(1turn);
		-ms-transform: rotate(1turn);
		transform: rotate(1turn);
    }
}


