#searchPage{
	position: relative;
	display: block;
	width: 100%;
	height: 78vh;
	opacity: 0;
	padding-top: 15px;
	overflow: hidden;
}
.ShowMove{
	opacity: 0;
	-webkit-animation: myfirst .5s forwards;
	        animation: myfirst .5s forwards;
}	
@-webkit-keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}
#searchPage input{
	position: absolute;
    width: 92%;
    height: 35px;
    padding: 0 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 30px;
    border: none;
 	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%); 
	left: 50%;
	background: url('../image/icon/search_bg.png') #ebecec no-repeat 2.5vw center ;
	outline: none;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}

#searchTrace{
	position: absolute;
	display: block;
	width: 100%;
	height: 75vh;
	z-index: 1;
	overflow-y:scroll;
	font-size: 4vw;
	top:55px;
}


#searchTrace h2{
	font-size: 5vw;
	text-align: center;
	color: #999;
	padding-top: 10%;
}
#searchTrace ul li{
	border-bottom: .1vh #e3e3e3 solid;
	padding: 3vh 12vw 2vh;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	width: 100vw;
	background: url('../image/icon/search_trace.png') no-repeat 3% 55%;
	position: relative;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
#searchTrace ul li b{
	font-weight: normal;
	font-size: 4.8vw;
	color: #666;

}
#searchTrace ul li span{
	width: 10%;
	height: 100%;
	background: url('../image/icon/search_trace_close.png') no-repeat center;
	background-size:16px;
	position: absolute;
	right: 3%;
	top: 55%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}

#searchResult{
	position: absolute;
	display: block;
	width: 100%;
	height: 75vh;
	top: 9vh;
	z-index: 2;
	overflow-y:scroll;
	background: #fff;
	display: none;
}
#searchResult h2{
	font-size: 5vw;
	text-align: center;
	color: #999;
	padding-top: 10%;
}
#searchResult ul li{
	border-bottom: .1vh #e3e3e3 solid;
	padding:3vh 4vw 3vh 10vw;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	width: 100vw;
	background: url('../image/icon/search_bg.png') no-repeat 3.5% 55%;
	position: relative;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	font-weight: normal;
	font-size:4.5vw;
	color: #666;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
#searchResult ul li b{
	display: block;
	width: 60%;
	float: left;
	font-weight: normal;
	padding: 0px 5%;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
#searchResult ul li span{
	display: block;
	width:26%;
	float: right;
	padding: 0px 2%;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


