.router_container{
	position: absolute;
	display: block;
	width: 100%;
	height: 83vh;
	overflow-y:scroll;
	opacity: 0;
}
.recommend_container{
	width: 100%;
	padding-top: 3vh;
	height:65vh;
}
.recommend_container h2{
	position: relative;
    padding-left: 9px;
    margin-bottom: 3vh;
    font-size: 5.8vw;
    height: 20px;
    line-height: 20px;
    font-weight: normal;
}
.recommend_container h2:after{
	content: " ";
    position: absolute;
    left: 0;
    top: 50%;
    width: 2px;
    height: 16px;
    background-color: #d33a31;
    -webkit-transform: translateY(-45%);
    -ms-transform: translateY(-45%);
    transform: translateY(-45%);
}
#recommend_music_list{
	width: 100%;
	height: 320px;
}
#recommend_music_list ul{
	/*display: block;*/
}
#recommend_music_list ul li{
	display: block;
	width: 32.8%;
	float: left;
	height:140px;
	margin-bottom: 3vh;
	position: relative;
}
#recommend_music_list ul li:nth-child(2){
	margin-left: 0.8%;
}
#recommend_music_list ul li:nth-child(3){
	margin-left: 0.8%;
}
#recommend_music_list ul li:nth-child(5){
	margin-left: 0.8%;
}
#recommend_music_list ul li:nth-child(6){
	margin-left: 0.8%;
}
#recommend_music_list ul li strong{
	display: block;
	width: 100%;
	height: 70%;
}
#recommend_music_list ul li span{
	color: #fff;
	position: absolute;
	right: 2%;
	top:1%;
	font-size: 3.9vw;
}
#recommend_music_list ul li span:after{
	content: " ";
	display: block;
	float: left;
	-webkit-transform: translateY(25%) translateX(-30%);
	-ms-transform: translateY(25%) translateX(-30%);
	transform: translateY(25%) translateX(-30%);
    width: 3.5vw;
    height:3.5vw;
    background:url('../image/icon/erji.png');
    background-size: cover;
}
#recommend_music_list ul li p{
	font-size: 4.5vw;
	font-weight: normal;
	width: 96%;
	padding:2% 2% 0% 2%;
    display: -webkit-box; 
    -webkit-line-clamp: 2; 
    overflow: hidden; 
	/* autoprefixer: off */
	-webkit-box-orient: vertical; 
	/* autoprefixer: on */
    line-height: 1.2;
}
#newsong_list{
	width: 100%;
}
#newsong_list ul li{
	position: relative;
	width: 94%;
	padding: 3%;
	border-bottom: 1px solid #eaeaea;

}
#newsong_list ul li:before{
	content: '';
	display: inline-block;
	position: absolute;
	right: 3%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
    width: 22px;
    height: 22px;
    background: url('../image/index_icon_2x.png') no-repeat;
    background-position: -24px 0;
    background-size: 166px 97px;
}
#newsong_list ul li span{
	display: block;
	width: 90%;
	font-size: 5.5vw;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
#newsong_list ul li span strong{
	font-weight: normal;
	color: #888;
	margin-left: 4px;
}
#newsong_list ul li b{
	font-weight: normal;
	display: block;
	color: #888;
	font-size:4vw;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
	
}
#newsong_list ul li b:after{
	content:'';
	display: inline-block;
	float: left;
    width: 12px;
    height: 8px;
    margin-right: 4px;
	background: url('../image/index_icon_2x.png') no-repeat;
	background-size: 166px 97px;
	-webkit-transform: translateY(55%);
	-ms-transform: translateY(55%);
	transform: translateY(55%);
}
.ShowMove{
	opacity: 0;
	-webkit-animation: myfirst .5s forwards;
	        animation: myfirst .5s forwards;
}	
@-webkit-keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}


