#hotmusic_container{
	position: absolute;
	display: block;
	width: 100%;
	height: 83vh;
	overflow-y:scroll;
	opacity: 0;
}
.hotmusicTopImg{
	width: 93.5%;
	height: 114px;
	position: relative;
    overflow: hidden;
    background: url('../image/hot_music_bg_2x.jpg') no-repeat;
    background-size: cover;
    padding-left:6.5%;
    padding-top: 15px;
}
.hotmusicTopImg span{
	display: block;
	width: 142px;
    height: 67px;
    background: url('../image/index_icon_2x.png') no-repeat;
    background-position: -24px -30px;
    background-size: 166px 97px;
    margin-bottom:10px;
}
.hotmusicTopImg b{
	display: block;
    color: hsla(0,0%,100%,.8);
    font-size: 12px;
    -webkit-transform: scale(.91);
    -ms-transform: scale(.91);
        transform: scale(.91);
    -webkit-transform-origin: left top;
    -ms-transform-origin: left top;
        transform-origin: left top;
    font-weight: normal;
}

#hotMusicBox{
	width: 100%;
}
#hotMusicBox ul li{
	width: 100%;
	display: block;
	height: 50px;
	position: relative;
	padding: 2% 0px 1% 0px;
	border-bottom: #ddd 1px solid;
}
#hotMusicBox ul li:before{
	content: '';
	display: inline-block;
	position: absolute;
	right: 3%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
    width: 22px;
    height: 22px;
    background: url('../image/index_icon_2x.png') no-repeat;
    background-position: -24px 0;
    background-size: 166px 97px;
}
#hotMusicBox ul li b{
	display: block;
	width:12%;
	color: #999;
	height: 100%;
	line-height: 50px;
	font-weight: normal;
	font-size: 5vw;
	text-align: center;
	float: left;
}
#hotMusicBox ul li:nth-child(1) b,#hotMusicBox ul li:nth-child(2) b,#hotMusicBox ul li:nth-child(3) b{
	color: #df3436;
}
#hotMusicBox ul li p{
	display: block;
	width: 70%;
	height: 50%;
	overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    font-size: 5.3vw;
    line-height: 25px;
}
#hotMusicBox ul li p span{
	color: #888;
}

#hotMusicBox ul li strong{
	font-size: 12px;
    color: #888;
    font-weight: normal;
    display: block;
    height: 50%;
    width: 80%;
    line-height: 25px;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.ShowMove{
	opacity: 0;
	-webkit-animation: myfirst .5s forwards;
	        animation: myfirst .5s forwards;
}	
@-webkit-keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}	
@keyframes myfirst
{
	0%{opacity: 0;}
    100%{opacity: 1;}
}


