.header{
	background:url('../image/header_bg.png') no-repeat;
    background-size: cover;
	height: 10vh;
	width: 100%;
	position: relative;
	padding: 0 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.topsvg {
    width:25vh;
    height: 10vh;
}

#music_icon{
    width:6vh;
    height:5.5vh;
    position: fixed;
    z-index: 999;
    top: 0.5rem;
    right: 4%;
    background: url(../image/icon/music_icon.png) no-repeat;
    background-size: 512px 5.5vh;
    z-index: 1;
    cursor: pointer;
    -webkit-animation: a_music 1s steps(16) infinite 0s;
}
.mute {
    animation-play-state:paused !important;
    -webkit-animation-play-state:paused !important;
}
@-webkit-keyframes a_music{
	100% {
	    background-position: -512px 0px;
	}
}
@keyframes a_music{
	100% {
	    background-position: -512px 0px;
	}
}



